const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start () {
        this.label.string = "loading";

        let longtext = `

        
        
        
         
         
        
        
         
        
        
        
        
        
         
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
         
        
        
        
        
        
        
        
        
        `;
        longtext = longtext.split("\n").map(x=>x.trim()).filter(x=>x.length > 0).map(x=> this.split_by_length(x, 40)).join("\n");
        
        this.scheduleOnce(()=>{
            this.label.string = longtext;
        }, 0);
    }

    split_by_length(str, len) {
        let m = Array.from(str);
        let idx = 0;
        let total = m.length;
        let ret = [];
        while(idx < total) {
            let l = Math.min(len, total - idx);
            if(l > 0) {
                ret.push(m.slice(idx, l + idx).join(""));
                idx += l;
            }else {
                break;
            }
        }
        return ret.join("\n");
    }
    // update (dt) {}
}
